import {sortBy, uniqBy} from 'lodash'

export default {
  SET_DATA_STATE(state, {dataState}) {
    state.state = dataState
  },

  SET_OFFSET(state, payload) {
    state.offset = payload
  },

  SET_COUNT(state, payload) {
    state.count = payload
  },

  set_loading(state, {type, status}) {
    state[type].isLoading = status
  },

  SET_LEADS(state, payload) {
    // state.leads = sortBy(uniqBy(state.leads.concat(payload), 'id'), lead => lead.id)
    state.leads = uniqBy(payload, 'id')
  },

  set_lead(state, payload) {
    const newLead = state.leads.find(lead => lead.id === payload.id)

    if (newLead) {
      state.leads = state.leads.map(lead => {
        if (lead.id === payload.id) {
          return payload
        }

        return lead
      })
    } else {
      state.leads.push(payload)
    }
  },

  edit_lead(state, payload) {
    state.leads = state.leads.map(lead => {
      if (lead.id === payload.id) {
        return Object.assign(lead, payload)
      }

      return lead
    })
  },

  set_active_lead(state, payload) {
    state.activeLead = payload
  },

  set_active_account(state, payload) {
    state.activeAccount = payload
  },

  set_lead_assign(state, {lead, marked}) {
    state.leads = state.leads.map(item => {
      if (item.id === lead.id) {
        item.assigned = marked
      }

      return item
    })
  },

  set_lead_discarded(state, {lead, marked}) {
    state.leads = state.leads.map(item => {
      if (item.id === lead) {
        item.discarded = marked
      }

      return item
    })
  },
}



