











































import Vue from 'vue'
import Component from 'vue-class-component'
import {Watch} from 'vue-property-decorator'
import {debounce} from 'lodash'
import Sources from '../../../components/_mixins/Sources'
import MessagesItems from './MessagesItems.vue'

@Component({
  mixins: [Sources],
  components: {MessagesItems},
  props: {},
})

export default class MessagesChat extends Vue {
  activeTab = null

  @Watch('$route')
  onRouteChange() {
    this.$store.commit('Messages/CLEAR_HISTORY')
    this.getLead()
  }

  get activeLead() {
    return this.$store.getters['Leads/getActiveLead']
  }

  get activeAccount() {
    return this.$store.getters['Leads/getActiveAccount']
  }

  get accounts() {
    return this.activeLead.accounts
  }

  getLinkToAccount(account) {
    return {
      name: 'messages.account',
      params: {
        lead: this.activeLead.id,
        account: account.id,
      },
    }
  }

  getLead() {
    if (this.$route.params.lead) {
      this.$store.dispatch('Leads/getByID', this.$route.params.lead)
        .then(lead => {
          if (this.$route.params.account) {
            const account = lead.accounts.find(acc => acc.id === parseInt(this.$route.params.account, 10))
            if (account) {
              this.$store.commit('Leads/set_active_account', account.id)
            }
          }
        })
    }
  }

  activated() {
    this.getLead()
  }

  deactivated() {
    this.activeTab = null
    this.$store.commit('Leads/set_active_lead', null)
    this.$store.commit('Leads/set_active_account', null)
  }

  onScroll(event) {
    // console.log()
    // console.log(window.pageYOffset || document.documentElement.scrollTop)
  }

  mounted() {
    // window.scrollTo(0, document.documentElement.clientHeight)
  }

  created() {
    this.getLead()
  }
}



