import Messages from '../../views/messages/index.vue'
import Dialogs from '../../views/messages/dialogs/index.vue'
import Chat from '../../views/messages/chat/index.vue'
import Utils from '../utils/utils'

export default {
  path: '/im',
  component: Messages,
  children: [
    {
      path: '',
      name: 'messages',
      component: Dialogs,
      meta: {
        title: Utils.getTitle('Диалоги'),
      },
    }, {
      path: 'lead:lead',
      name: 'messages.lead',
      component: Chat,
      meta: {
        title: Utils.getTitle('Диалоги'),
      },
      children: [{
        path: 'account:account',
        name: 'messages.account',
        title: Utils.getTitle('Диалоги'),
        component: Chat,
        meta: {
          title: Utils.getTitle('Диалоги'),
        },
      }],
    },
  ],
}



