









import Vue from 'vue'
import Component from 'vue-class-component'

@Component({
  props: {},
})

export default class ScrollContainer extends Vue {
  $refs = {
    scrollContainer: HTMLElement,
  }

  onScroll(event) {
    this.$emit('onScroll', event.target.scrollTop)
  }

  scrollToTop() {
    if (this.$refs.scrollContainer) {
      this.$refs.scrollContainer.scrollTop = 0
    }
  }

  scrollToTop(value: number = 0) {
    if (this.$refs.scrollContainer) {
      this.$refs.scrollContainer.scrollTop = value
    }
  }

  scrollToBottom() {
    if (this.$refs.scrollContainer && this.$refs.scrollContainerInner) {
      this.$refs.scrollContainer.scrollTop = this.$refs.scrollContainerInner.clientHeight
    }
  }
}



