import {cloneDeep, uniqBy} from 'lodash'
import moment from 'moment/moment'
import MessagesApi from '../../../api/Messages'
import SourcesApi from '../../../api/Sources'
import config from '../../../../config/config'
import Emoji from '../../../utils/Emoji'
import Parse from '../../../utils/Parse'

function concatMessages(oldMessages, newMessages) {
  let newEntries = cloneDeep(oldMessages).reverse()

  newEntries = uniqBy(newEntries.concat(newMessages), 'unique').reverse()

  oldMessages = newEntries


  oldMessages.reduce((previousValue, currentValue, index, array) => {
    let sibling = false
    const nextValue = array[++index] || {}

    const out = nextValue.out === currentValue.out
    let source = false

    if (nextValue.source) {
      source = nextValue.source.id === currentValue.source.id
    }

    const comment = currentValue.type !== 3

    if (nextValue.stamp) {
      let nextDate = moment.unix(nextValue.stamp).format('YYYY_MM_DD_HH')
      let currentDate = moment.unix(currentValue.stamp).format('YYYY_MM_DD_HH')

      sibling = nextDate === currentDate

      if (sibling) {
        nextDate = moment.unix(nextValue.stamp).format('YYYY_MM_DD_HH_mm')
        currentDate = moment.unix(currentValue.stamp).format('YYYY_MM_DD_HH_mm')

        sibling = Math.abs((parseInt(nextDate.split('_')[3], 10) * 60 + parseInt(nextDate.split('_')[4], 10)) - (parseInt(currentDate.split('_')[3], 10) * 60 + parseInt(currentDate.split('_')[4], 10))) < config.messageSiblingMinutes
      }
    }

    currentValue.sibling = out && source && sibling && comment
    currentValue.send = 1

    return currentValue
  }, 0)

  return oldMessages
}

export default {
  async dialogs({commit, rootState}, request) {
    commit('SET_DATA_STATE', {type: 'dialogs', dataState: config.dataStates.loading})
    let result = []

    try {
      result = await MessagesApi
        .dialogs(request, rootState.System.apiURLs.messages)
        .then(response => {
          const {sources, count} = response
          let {dialogs} = response

          dialogs = dialogs.map(dialog => {
            dialog.last_peer.lead = dialog.lead

            if (dialog.last_peer.last_message && dialog.last_peer.last_message.text) {
              dialog.last_peer.last_message.text = Emoji.emojiToHtml(dialog.last_peer.last_message.text)
            }

            return dialog
          })

          commit('SET_DIALOGS', dialogs)
          commit('SET_DIALOGS_COUNT', count)
          commit('SET_DIALOGS_SOURCES', sources)

          commit('SET_DATA_STATE', {type: 'dialogs', dataState: config.dataStates.loaded})

          return dialogs
        })
    } catch (error) {
      console.error(error)
      commit('SET_DATA_STATE', {
        type: 'dialogs',
        dataState: config.dataStates.failed,
      })
    }

    return result
  },

  async history({dispatch, commit, state, rootState}, request) {
    commit('SET_DATA_STATE', {type: 'history', dataState: config.dataStates.loading})

    let result = []

    try {
      result = await MessagesApi
        .history(request, rootState.System.apiURLs.messages)
        .then(response => {
          const {count, peers} = response

          const messages = Parse.parseArray(response, {
            target: response.messages,
            params: [{
              name: 'author',
              helperArray: 'users',
            }, {
              name: 'source',
              helperArray: 'sources',
            }],
          }).map(message => {
            if (message.text) {
              // message.text = Emoji.emojiToHtml(message.text)
            }

            // 0 - ошибка
            // 1 - успешно
            // 2 - в ожидании

            message.send = 1

            return message
          })

          commit('SET_HISTORY_PEERS', peers)
          commit('SET_HISTORY_COUNT', count)
          commit('SET_DATA_STATE', {type: 'history', dataState: config.dataStates.loaded})

          return messages
        })
    } catch (error) {
      console.log(error)
      commit('SET_DATA_STATE', {
        type: 'history',
        dataState: config.dataStates.failed,
      })
    }

    const oldMessages = cloneDeep(state.history.messages)
    const newMessages = cloneDeep(result)

    commit('SET_HISTORY_MESSAGES', concatMessages(oldMessages, newMessages))

    const sources = await dispatch('sources', {account: request.account})

    if (!request.offset && sources.length && result.length) {
      const source = sources.find(item => item.id === result[0].source.id)

      if (source) {
        commit('SET_SOURCES_ACTIVE', source)
      } else {
        commit('SET_SOURCES_ACTIVE', sources[0])
      }
    }

    return result
  },

  async sources({commit}, request) {
    commit('SET_DATA_STATE', {type: 'sources', dataState: config.dataStates.loading})

    return SourcesApi
      .forAccount(request)
      .then(({sources}) => {
        commit('SET_SOURCES', sources)

        commit('SET_DATA_STATE', {type: 'sources', dataState: config.dataStates.loaded})

        return sources
      })
  },

  async send({dispatch, state, rootState}, request) {
    let result = {
      messages: [],
      sources: [],
      users: [],
    }
    const random = +new Date() - (parseInt(+new Date() / 1000, 10) * 1000)

    const message = {
      account: request.account,
      author: null, // Вставить правильный
      text: request.message,
      source: state.sources.sources.find((source) => source.id === request.source),
      type: 1,
      out: true,
      // random: +new Date() - (parseInt(+new Date() / 1000, 10) * 1000),
      random,
      stamp: parseInt(+new Date() / 1000, 10),
      send: 2,
      unread: true,
    }

    await dispatch('message_new', {message, account: request.account})

    try {
      result = await MessagesApi
        .sendMessage({...request, random}, rootState.System.apiURLs.messages)
    } catch (error) {
      console.log(error)

      message.send = 0
      await dispatch('message_new', {message, account: request.account})
    }

    result = await dispatch('messages_new', {...result, account: request.account})

    return result
  },

  async repeatSend({dispatch, rootState}, request) {
    let result = {
      messages: [],
      sources: [],
      users: [],
    }

    const message = request

    await dispatch('message_new', {message, account: request.account})

    try {
      result = await MessagesApi
        .sendMessage(request, rootState.System.apiURLs.messages)
    } catch (error) {
      console.log(error)

      message.send = 0
      await dispatch('message_new', {message, account: request.account})
    }

    result = await dispatch('messages_new', {...result, account: request.account})

    return result
  },

  deleteMessage({commit}, request) {
    commit('DELETE_HISTORY_ENTRY', request)
  },

  async read({dispatch, rootState}, request) {
    await MessagesApi.read(request, rootState.System.apiURLs.messages)
  },


  // Events

  messages_new({dispatch}, payload) {
    const messages = Parse.parseArray(payload, {
      target: payload.messages,
      params: [{
        name: 'author',
        helperArray: 'users',
      }, {
        name: 'source',
        helperArray: 'sources',
      }],
    }).map(message => {
      if (message.text) {
        // message.text = Emoji.emojiToHtml(message.text)
      }

      // 0 - ошибка
      // 1 - успешно
      // 2 - в ожидании

      message.send = 1

      return message
    })

    messages.forEach(message => {
      dispatch('message_new', {message, account: payload.account})
    })
  },

  message_new({commit, rootState}, payload) {
    let {message} = payload

    if (payload.sources) {
      message = Parse.parseObject(payload, {
        target: payload.message,
        params: [{
          name: 'author',
          helperArray: 'users',
        }, {
          name: 'source',
          helperArray: 'sources',
        }],
      })


      if (message.text) {
        // message.text = Emoji.emojiToHtml(message.text)
      }

      message.send = 1
    }


    if (payload.account === rootState.Leads.activeAccount) {
      commit('SET_HISTORY_ENTRY', message)
    }

    if (payload.dialog) {
      const dialog = cloneDeep(payload.dialog)
      dialog.last_peer.lead = payload.dialog.lead

      if (payload.dialog.last_peer.last_message && payload.dialog.last_peer.last_message.text) {
        // dialog.last_peer.last_message.text = Emoji.emojiToHtml(payload.dialog.last_peer.last_message.text)
      }

      commit('SET_DIALOG', dialog)
    }
  },

  message_typing({commit}, payload) {
    commit('SET_DIALOGS_TYPING', payload)
  },

  message_edit({commit, rootState}, payload) {
    if (payload.lead.account.id === rootState.Leads.activeAccount) {
      payload.sources.forEach((source) => {
        // console.log(source)
      })

      // commit('SET_HISTORY_ENTRY_READ', payload.source.id)
    }
  },

  account_read({commit, rootState}, payload) {
    if (payload.lead.account.id === rootState.Leads.activeAccount) {
      commit('SET_HISTORY_ENTRY_READ', payload)
    }
  },

  source_read({commit, rootState}, payload) {
    if (payload.lead.account.id === rootState.Leads.activeAccount) {
      commit('SET_DIALOGS_READ', payload.lead)
      commit('SET_HISTORY_ENTRY_READ', payload)
    }
  },

  lead_taken({commit}, payload) {
    commit('SET_DIALOG_ASSIGNED', payload)
  },
  lead_restored({commit}, payload) {
    commit('SET_DIALOG_RESTORED', payload)
  },
  lead_discarded({commit}, {lead}) {
    commit('SET_DIALOG_DISCARDED', lead)
  },
}



