export default {
  getTitle: (str) => `${str} | HiveCRM`
}

/**
 * Проверяет вхождение search в string
 *
 * @param string
 * @param search
 * @returns {boolean}
 */
export function includeString(string, search) {
  return string.indexOf(search) > -1
}



