import {cloneDeep} from 'lodash'
import MessagesApi from '../../../api/Messages'
import HotApi from '../../../api/Hot'
import LeadsApi from '../../../api/Leads'

import config from '../../../../config/config'
import Parse from '../../../utils/Parse'
import Emoji from '../../../utils/Emoji'

export default {
  async hot({commit, rootState}, request) {
    commit('SET_DATA_STATE', {type: 'hot', dataState: config.dataStates.loading})
    let result = []

    try {
      result = await MessagesApi
        .dialogs(request, rootState.System.apiURLs.messages)
        .then(response => {
          const {sources, count} = response
          let {dialogs} = response

          dialogs = dialogs.map(dialog => {
            dialog.last_peer.lead = dialog.lead

            if (dialog.last_peer.last_message && dialog.last_peer.last_message.text) {
              dialog.last_peer.last_message.text = Emoji.emojiToHtml(dialog.last_peer.last_message.text)
            }

            return dialog
          })

          commit('SET_HOT', dialogs)
          commit('SET_HOT_COUNT', count)
          commit('SET_HOT_SOURCES', sources)
          //
          commit('SET_DATA_STATE', {type: 'hot', dataState: config.dataStates.loaded})

          return dialogs
        })
    } catch (error) {
      console.error(error)
      commit('SET_DATA_STATE', {
        type: 'hot',
        dataState: config.dataStates.failed,
      })
    }

    return result
  },

  async process({commit, rootState}, request) {
    commit('SET_DATA_STATE', {type: 'process', dataState: config.dataStates.loading})
    let result = []
    console.log(request)
    try {
      result = await MessagesApi
        .dialogs(request, rootState.System.apiURLs.messages)
        .then(response => {
          const {sources, count} = response
          let {dialogs} = response

          dialogs = dialogs.map(dialog => {
            dialog.last_peer.lead = dialog.lead

            if (dialog.last_peer.last_message && dialog.last_peer.last_message.text) {
              dialog.last_peer.last_message.text = Emoji.emojiToHtml(dialog.last_peer.last_message.text)
            }

            return dialog
          })

          commit('SET_PROCESS', dialogs)
          commit('SET_PROCESS_COUNT', count)
          commit('SET_PROCESS_SOURCES', sources)

          commit('SET_DATA_STATE', {type: 'process', dataState: config.dataStates.loaded})

          return dialogs
        })
    } catch (error) {
      console.error(error)
      commit('SET_DATA_STATE', {
        type: 'process',
        dataState: config.dataStates.failed,
      })
    }

    return result
  },

  getPlanner({commit}, {from, to, offset, count, reverse}) {
    commit('SET_DATA_STATE', {type: 'hotPlanner', dataState: config.dataStates.loading})

    return HotApi
      .getPlanner({from, to, offset, count, reverse})
      .then(response => {
        const params = [
          {
            name: 'lead',
            helperArray: 'leads',
          },
          {
            name: 'product',
            helperArray: 'products',
          },
          {
            name: 'source',
            helperArray: 'sources',
          },
          {
            name: 'manager',
            helperArray: 'managers',
          },
        ]

        commit('SET_PLANNER_ITEMS', Parse.parseArray(response, {
          target: response.items,
          params,
        }))
        commit('SET_PLANNER_OFFSET', offset + count)

        commit('SET_DATA_STATE', {
          type: 'hotPlanner',
          dataState: config.dataStates.loaded,
        })

        return response.items
      })
      .catch(() => {
        commit('SET_DATA_STATE', {
          type: 'hotPlanner',
          dataState: config.dataStates.failed,
        })
      })
  },

  processed({dispatch}, lead) {
    return LeadsApi
      .processed(lead)
      .then(({marked}) => {
        dispatch('lead_processed', lead)

        return marked
      })
  },

  markPlannerNote({commit}, {note: {id}}) {
    commit('NOTE_MARK', {id})
  },

  /**
   * Events
   */

  lead_processed({commit}, leadId) {
    commit('SET_LEAD_PROCESSED', leadId)
  },

  note_add({commit}, payload) {
  },

  funnel_delete({commit}, payload) {
    commit('PLANNER_DELETE', payload)
  },

  note_mark({commit}, {note}) {
    commit('NOTE_MARK', note)
  },

  message_typing({commit}, {lead, source}) {
    commit('SET_LEAD_TYPING', {lead, source})
  },

  message_new({commit}, {dialog}) {
    commit('UPDATE_DIALOG', {...dialog})
  },

  lead_taken({commit}, payload) {
    commit('UPDATE_LEAD_ASSIGNED', payload)
  },

  lead_restored({commit}, payload) {
    commit('UPDATE_LEAD_RESTORED', payload)
  },
  lead_discarded({commit}, {lead}) {
    commit('UPDATE_LEAD_DISCARDED', {lead, discarded: true})
  },
}



