import config from '../../../../config/config'
import SystemApi from '../../../api/System'

export default {
  async state({commit}, request) {
    commit('SET_DATA_STATE', config.dataStates.loading)
    let result = {}

    try {
      result = await SystemApi.state()

      commit('SET_DATA_STATE', config.dataStates.loaded)
      commit('SET_STATE', result)
    } catch (error) {
      commit('SET_DATA_STATE', config.dataStates.failed)

      throw new Error('Произошла ошибка при получении данных')
    }

    return result
  },
}



