interface IEventKey {
  key: string
  server: string
  socket: string
  stamp: string;
}

interface IUpdate {
  data: any,
  type: string
}

class Socket {
  private reconnectSocketTimeout: number = 20000;
  public eventKeyGet: boolean = false

  /**
   * Класс для работы с сокетами
   *
   * @param {string} accessToken
   * @param $http
   * @param $store
   * @param EventBus
   */
  public constructor(public accessToken: string,
                     public $http: any,
                     public $store: any,
                     public EventBus: any) {

  }

  public connect() {
    if (typeof window['WebSocket'] === 'function') {
      this.connectWebSocket()
    }
  }

  private connectWebSocket() {
    this.$http.get('events/key', {params: {access_token: this.accessToken}})
      .then((response): IEventKey => {
        const {key, socket, server, messages} = response;

        this.$store.commit('System/SET_URLS', {socket, server, messages})

        this.initWebSocket(socket, key);

        this.eventKeyGet = true;

        return response
      })
  }

  /**
   * Инициализация работы WebSocket
   *
   * @param {string} socket
   * @param {string} key
   */
  private initWebSocket(socket: string, key: string) {
    const webSocket = new WebSocket(socket);

    webSocket.onopen = () => {
      if (process.env.NODE_ENV === 'development') {
        console.log(`Сокет открыт ${socket}`)
      }

      // Записываем ключ
      webSocket.send(JSON.stringify({
        content: key,
        type: 'auth',
      }))
    };

    webSocket.onclose = event => {
      if (event.wasClean) {
        if (process.env.NODE_ENV === 'development') {
          console.log('Реконнект', event)
        }

        setTimeout(this.connectWebSocket.bind(this), this.reconnectSocketTimeout, socket, key)
      } else {
        if (process.env.NODE_ENV === 'development') {
          console.log('Обрыв соединения', {code: event.code, reason: event.reason})
        }

        setTimeout(this.connectWebSocket.bind(this), this.reconnectSocketTimeout, socket, key)
      }
    };

    webSocket.onmessage = ({data}) => {
      const {content, state} = JSON.parse(data);
      const {updates} = content

      if (state === 'OK') {
        this.setUpdates(updates)
      }
    };

    webSocket.onerror = (error) => {
      console.error(error);
      throw new Error('Ошибка сокета')
    }
  }

  /**
   * Установление обновлений
   *
   * @param {IUpdate[]} updates
   */
  private setUpdates(updates: IUpdate[]) {
    updates.forEach((update: IUpdate) => {
      if (process.env.NODE_ENV === 'development') {
        console.log(`%c Новое событие "${update.type}" `, 'color: #00e2ff');
      }

      this.$store.dispatch(`events/${update.type}`, update.data)
      this.EventBus.$emit(`events/${update.type}`, update.data)
    })
  }
}

export default Socket



