export default {
  SET_DATA_STATE(state, payload) {
    state.state = payload
  },

  SET_STATE(state, payload) {
    state.system = Object.assign({}, {...state.system}, {...payload})
  },

  SET_URLS(state, payload) {
    state.apiURLs = payload
  }
}


