import {cloneDeep, uniqBy, uniqWith} from 'lodash'
import config from '../../../../config/config'

export default {
  SET_DATA_STATE(state, {type, dataState}) {
    state[type].state = dataState
  },

  SET_HOT(state, payload) {
    state.hot.dialogs = uniqWith(state.hot.dialogs.concat(payload), (dialog, other) => {
      return dialog.lead.id === other.lead.id
    })
  },

  CLEAR_HOT(state) {
    state.hot.state = config.dataStates.notAsked
    state.hot.dialogs = []
    state.hot.sources = []
    state.hot.count = 0
  },

  SET_HOT_DIALOG(state, payload) {
    const index = state.hot.dialogs.indexOf(state.hot.dialogs.find(dialog => dialog.lead.id === payload.lead.id))

    if (index > -1) {
      state.hot.dialogs.splice(index, 1)
    }

    state.hot.dialogs.unshift(payload)
  },

  SET_HOT_COUNT(state, payload) {
    state.hot.count = payload
  },

  SET_HOT_SOURCES(state, payload) {
    state.hot.sources = uniqBy(state.hot.sources.concat(payload), 'id')
  },

  SET_HOT_READ(state, payload) {
    state.hot.dialogs = state.hot.dialogs.map(dialog => {
      if (dialog.lead.id === payload.id) {
        dialog.unread = 0
      }

      return dialog
    })
  },

  SET_PROCESS(state, payload) {
    state.process.dialogs = uniqWith(state.process.dialogs.concat(payload), (dialog, other) => {
      return dialog.lead.id === other.lead.id
    })
  },

  CLEAR_PROCESS(state) {
    state.process.state = config.dataStates.notAsked
    state.process.dialogs = []
    state.process.sources = []
    state.process.count = 0
  },

  SET_PROCESS_DIALOG(state, payload) {
    const index = state.process.dialogs.indexOf(state.process.dialogs.find(dialog => dialog.lead.id === payload.lead.id))

    if (index > -1) {
      state.process.dialogs.splice(index, 1)
    }

    state.process.dialogs.unshift(payload)
  },

  SET_PROCESS_COUNT(state, payload) {
    state.process.count = payload
  },

  SET_PROCESS_SOURCES(state, payload) {
    state.process.sources = uniqBy(state.process.sources.concat(payload), 'id')
  },

  SET_PROCESS_READ(state, payload) {
    state.process.dialogs = state.process.dialogs.map(dialog => {
      if (dialog.lead.id === payload.id) {
        dialog.unread = 0
      }

      return dialog
    })
  },

  // OLD
  SET_LEAD_PROCESSED(state, leadId) {
    const index = state.process.dialogs.indexOf(state.process.dialogs.find(dialog => dialog.lead.id === leadId))

    if (index > -1) {
      state.process.dialogs.splice(index, 1)
    }
  },

  SET_CLEAR_PROCESS(state) {
    state.process.dialogs = []
    state.process.sources = []
    state.process.offset = 0
  },

  SET_PLANNER_ITEMS(state, payload) {
    state.hotPlanner.items = uniqWith(state.hotPlanner.items.concat(payload), (item, other) => {
      return (item.id === other.id && item.type === other.type)
    })
  },

  PLANNER_DELETE(state, payload) {
    const index = state.hotPlanner.items.indexOf(state.hotPlanner.items.find(item => item.id === payload.funnel.id))

    if (index > -1) {
      state.hotPlanner.items.splice(index, 1)
    }
  },

  SET_PLANNER_DIALOGS(state, payload) {
    state.hotPlanner.dialogs = uniqWith(state.hotPlanner.dialogs.concat(payload), (dialog, other) => {
      return dialog.lead.id === other.lead.id
    })
  },

  SET_PLANNER_SOURCES(state, payload) {
    state.hotPlanner.sources = payload
  },

  SET_PLANNER_REVERSE(state, payload) {
    state.hotPlanner.reverse = payload ? 1 : 0
  },

  SET_PLANNER_OFFSET(state, payload) {
    state.hotPlanner.offset = payload
  },

  SET_PLANNER_SHOW_MESSAGE(state, payload) {
    state.hotPlanner.items = state.hotPlanner.items.map(dialog => {
      if (payload.id === dialog.id) {
        dialog.isShow = payload.isShow
      }

      return dialog
    })
  },

  SET_CLEAR_PLANNER(state) {
    state.hotPlanner.items = []
    state.hotPlanner.leads = []
    state.hotPlanner.managers = []
    state.hotPlanner.products = []
    state.hotPlanner.sources = []
    state.hotPlanner.offset = 0
  },

  NOTE_MARK(state, payload) {
    const index = state.hotPlanner.items.indexOf(state.hotPlanner.items.find(item => item.id === payload.id))

    if (index > -1) {
      state.hotPlanner.items.splice(index, 1)
    }
  },

  SET_QUERY(state, payload) {
    state.query = payload
  },

  SET_LEAD_TYPING(state, {lead}) {
    state.hot.dialogs = state.hot.dialogs.map(dialog => {
      if (dialog.lead.id === lead.id) {
        const _lead = cloneDeep(dialog.lead)
        _lead.typing = !dialog.lead.typing
        dialog.lead = _lead
      }

      return dialog
    })

    state.process.dialogs = state.process.dialogs.map(dialog => {
      if (dialog.lead.id === lead.id) {
        const _lead = cloneDeep(dialog.lead)
        _lead.typing = !dialog.lead.typing

        dialog.lead = _lead
      }

      return dialog
    })
  },

  UPDATE_DIALOG(state, newDialog) {
    let hotDialog = false
    let processDialog = false

    state.hot.dialogs = state.hot.dialogs.map(dialog => {
      if (dialog.lead.id === newDialog.lead.id) {
        hotDialog = true

        return newDialog
      }

      return dialog
    })

    state.process.dialogs = state.process.dialogs.map(dialog => {
      if (dialog.lead.id === newDialog.lead.id) {
        processDialog = true
        return newDialog
      }

      return dialog
    })

    // Если он есть в новых заявках, то обновляем перемещаем его наверх
    if (hotDialog) {
      const index = state.hot.dialogs.indexOf(state.hot.dialogs.find(dialog => dialog.lead.id === newDialog.lead.id))

      if (index > -1) {
        state.hot.dialogs.splice(index, 1)
      }

      state.hot.dialogs.unshift(newDialog)
    }

    // Если он есть в процессе, то обновляем перемещаем его наверх
    if (processDialog) {
      const index = state.process.dialogs.indexOf(state.process.dialogs.find(dialog => dialog.lead.id === newDialog.lead.id))

      if (index > -1) {
        state.process.dialogs.splice(index, 1)
      }

      state.process.dialogs.unshift(newDialog)
    }

    if (!hotDialog && !processDialog) {
      if (newDialog.lead.assigned) {
        state.process.dialogs.unshift(newDialog)
      } else {
        state.hot.dialogs.unshift(newDialog)
      }
    }
  },

  UPDATE_LEAD_ASSIGNED(state, payload) {
    const hot = state.hot.dialogs.find(dialog => dialog.lead.id === payload.lead.id)
    const process = state.process.dialogs.find(dialog => dialog.lead.id === payload.lead.id)

    if (hot) {
      state.hot.dialogs = state.hot.dialogs.map(dialog => {
        if (dialog.lead.id === payload.lead.id) {
          return payload
        }

        return dialog
      })
    } else {
      state.hot.dialogs.unshift(payload)
    }

    if (process) {
      state.process.dialogs = state.process.dialogs.map(dialog => {
        if (dialog.lead.id === payload.lead.id) {
          return payload
        }

        return dialog
      })
    } else {
      state.process.dialogs.unshift(payload)
    }
  },

  UPDATE_LEAD_RESTORED(state, payload) {
    const hot = state.hot.dialogs.find(dialog => dialog.lead.id === payload.lead.id)
    const process = state.process.dialogs.find(dialog => dialog.lead.id === payload.lead.id)

    if (hot) {
      state.hot.dialogs = state.hot.dialogs.map(dialog => {
        if (dialog.lead.id === payload.lead.id) {
          return payload
        }

        return dialog
      })
    } else {
      state.hot.dialogs.unshift(payload)
    }

    if (process) {
      state.process.dialogs = state.process.dialogs.map(dialog => {
        if (dialog.lead.id === payload.lead.id) {
          return payload
        }

        return dialog
      })
    } else {
      state.process.dialogs.unshift(payload)
    }
  },

  UPDATE_LEAD_DISCARDED(state, payload) {
    state.hot.dialogs = state.hot.dialogs.map(dialog => {
      if (dialog.lead.id === payload.lead) {
        const currentLead = cloneDeep(dialog.lead)

        currentLead.discarded = payload.discarded
        dialog.lead = currentLead
      }

      return dialog
    })

    state.process.dialogs = state.process.dialogs.map(dialog => {
      if (dialog.lead.id === payload.lead) {
        const currentLead = cloneDeep(dialog.lead)

        currentLead.discarded = payload.discarded
        dialog.lead = currentLead
      }

      return dialog
    })
  },
}



