import config from '../../../../config/config'
import {includeString} from '../../../utils/utils'

export default {
  notAsked: state => (type = 'hot') => state[type].state === config.dataStates.notAsked,

  loading: state => (type = 'hot') => state[type].state === config.dataStates.loading,

  loaded: state => (type = 'hot') => state[type].state === config.dataStates.loaded,

  failed: state => (type = 'hot') => state[type].state === config.dataStates.failed,

  hot(state) {
    return state.hot.dialogs
  },

  hotDialog: state => lead => state.hot.dialogs.find(dialog => dialog.lead.id === lead),

  hotCount(state) {
    return state.hot.count
  },

  hotSources(state) {
    return state.hot.sources
  },

  process(state) {
    return state.process.dialogs
  },

  processCount(state) {
    return state.process.count
  },

  processSources(state) {
    return state.process.sources
  },


  hotPlannerItems(state) {
    let {items} = state.hotPlanner
    const query = state.query.toLowerCase()

    if (state.query.length) {
      items = items.filter(item => {
        const leadName = item.lead.name.toLowerCase()
        const name = ((item.type === 0 ? item.name : item.product.name) || '').toLowerCase()
        const content = item.content ? item.content.toLowerCase() : ''

        return includeString(leadName, query) || includeString(name, query) || includeString(content, query)
      })
    }

    return items
  },

  hotPlannerSources(state) {
    return state.hotPlanner.sources
  },

  hotPlannerOffset(state) {
    return state.hotPlanner.offset
  },

  hotPlannerReverse(state) {
    return state.hotPlanner.reverse
  },

  query(state) {
    return state.query
  },
}



