<template>
  <v-app :dark="failed">
    <template v-if="ready">
      <!--<template v-if="showInFullScreen && screenFull.accepted">-->
      <template v-if="true">
        <router-view/>
      </template>
      <template v-else>
        <v-app dark>
          <v-content>
            <v-container fluid fill-height>
              <v-layout column justify-center align-center>
                <v-flex shrink xs9 justify-center align-center>
                  <v-layout align-center>
                    <v-flex>
                      <p class="body-2 text-xs-center">
                        Приложение лучше отображается в полноэкранном режиме
                      </p>
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex shrink>
                  <v-btn outline color="placeholder" @click.prevent="refuseFullScreen">Отмена
                  </v-btn>
                  <v-btn outline color="primary" @click.prevent="acceptFullScreen">Включить</v-btn>
                </v-flex>
                <v-flex shrink>

                </v-flex>
              </v-layout>
            </v-container>
          </v-content>
        </v-app>
      </template>
    </template>
    <template v-if="!ready && !failed">
      <v-content>
        <v-container fluid fill-height>
          <v-layout justify-center align-center>
            <v-flex shrink>
              <v-progress-circular indeterminate :size="50" color="primary"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-content>
    </template>
    <template v-if="failed">
      <v-content>
        <v-container fluid fill-height>
          <v-layout justify-center align-center>
            <v-flex shrink>
              <p class="title text-xs-center primary--text">
                <v-avatar :size="128" tile>
                  <img src="/img/error.png">
                </v-avatar>
                <br>
                <br>
                {{ errorMessage }}
              </p>
            </v-flex>
          </v-layout>
        </v-container>
      </v-content>
    </template>
  </v-app>
</template>

<script>
  import Socket from './services/Socket.ts'
  import Api from './services/api/Api'
  import EventBus from './services/utils/EventBus'
  import * as screenfull from 'screenfull'

  export default {
    name: 'app',
    data() {
      return {
        socket: {},
        windowLoad: false,
        errorMessage: '',
        screenFull: {
          accepted: false,
          enabled: screenfull.enabled,
          isFullscreen: screenfull.isFullscreen,
        },
      }
    },
    computed: {
      notAsked() {
        return this.$store.getters['System/notAsked']
      },

      loading() {
        return this.$store.getters['System/loading']
      },

      loaded() {
        return this.$store.getters['System/loaded']
      },

      failed() {
        return this.$store.getters['System/failed']
      },

      system() {
        return this.$store.getters['System/system']
      },

      access() {
        return this.system.allowed && this.system.authorized && this.system.ready
      },

      windowLoaded() {
        if (this.$config.environment === 'development') {
          return true
        }

        return this.windowLoad
      },

      showInFullScreen() {
        if (this.screenFull.enabled) {
          return this.screenFull.isFullscreen
        }

        return true
      },

      ready() {
        const isLoginPage = this.$route.name === 'login'

        if (isLoginPage) {
          return this.loaded && this.windowLoaded && !this.access
        }

        if (isLoginPage && this.access) {
          location.pathname = '/'
        }

        return this.loaded && this.access && this.windowLoaded && this.socket.eventKeyGet
      },
    },

    watch: {
      system() {
        if (!this.system.allowed) {
          if (this.$cookie.get('token', this.$config.cookie)) {
            this.$cookie.delete('token', this.$config.cookie)
          }

          if (location.pathname.indexOf('login') < 1) {
            location.replace('/login')
          }
        }

        if (this.access) {
          if (!this.socket.eventKeyGet) {
            this.socket = new Socket(this.$token, Api, this.$store, EventBus)
            this.socket.connect()
          }
        }
      },
    },

    methods: {
      windowLoadHandler() {
        this.windowLoad = true
      },

      async getSystemState() {
        try {
          await this.$store.dispatch('System/state')
        } catch (error) {
          this.errorMessage = error.message
        }
      },

      acceptFullScreen() {
        this.screenFull.accepted = true
        screenfull.request()
      },

      refuseFullScreen() {
        this.screenFull.accepted = true
        screenfull.exit()
      },
    },

    created() {
      this.getSystemState()

      window.addEventListener('load', this.windowLoadHandler)

      if (screenfull.enabled) {
        screenfull.on('change', () => {
          this.screenFull.isFullscreen = screenfull.isFullscreen
        })
      }
    },

    mounted() {
      // console.log(window.pageYOffset || document.documentElement.scrollTop)
      console.log()
      window.addEventListener('scroll', function (e) {
        // console.log(e)
      })
      document.addEventListener('touchmove', function (e) {
        // e.preventDefault()
      })
    },
  }
</script>

<style lang="scss">
  @import "assets/styles/index";
</style>


