import actions from './actions'
import getters from './getters'
import mutations from './mutations'
import config from '../../../../config/config'

const state = {
  dialogs: {
    state: config.dataStates.notAsked,
    dialogs: [],
    sources: [],
    count: 0,
  },
  history: {
    state: config.dataStates.notAsked,
    messages: [],
    peers: [],
    count: 0,
  },
  sources: {
    state: config.dataStates.notAsked,
    sources: [],
    active: {},
    count: 0,
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



