import {uniqBy, uniqWith, sortBy, cloneDeep} from 'lodash'
import moment from 'moment'
import EventBus from '../../../utils/EventBus'
import config from '../../../../config/config'

export default {
  SET_DATA_STATE(state, {type, dataState}) {
    state[type].state = dataState
  },

  SET_DIALOGS(state, payload) {
    state.dialogs.dialogs = uniqWith(state.dialogs.dialogs.concat(payload), (dialog, other) => {
      return dialog.lead.id === other.lead.id
    })
  },

  CLEAR_DIALOGS(state) {
    state.dialogs.state = config.dataStates.notAsked
    state.dialogs.dialogs = []
    state.dialogs.sources = []
    state.dialogs.count = 0
  },

  SET_DIALOG(state, payload) {
    const index = state.dialogs.dialogs.indexOf(state.dialogs.dialogs.find(dialog => dialog.lead.id === payload.lead.id))

    if (index > -1) {
      state.dialogs.dialogs.splice(index, 1)
    }

    state.dialogs.dialogs.unshift(payload)
  },

  SET_DIALOG_ASSIGNED(state, payload) {
    const newDialog = state.dialogs.dialogs.find(item => item.lead.id === payload.lead.id)

    if (newDialog) {
      state.dialogs.dialogs = state.dialogs.dialogs.map(item => {
        if (item.lead.id === payload.lead.id) {
          return payload
        }

        return item
      })
    } else {
      state.dialogs.dialogs.unshift(payload)
    }
  },

  SET_DIALOG_RESTORED(state, payload) {
    const newDialog = state.dialogs.dialogs.find(item => item.lead.id === payload.lead.id)

    if (newDialog) {
      state.dialogs.dialogs = state.dialogs.dialogs.map(item => {
        if (item.lead.id === payload.lead.id) {
          return payload
        }

        return item
      })
    } else {
      state.dialogs.dialogs.unshift(payload)
    }
  },

  SET_DIALOG_DISCARDED(state, payload) {
    state.dialogs.dialogs = state.dialogs.dialogs.map(dialog => {
      if (dialog.lead.id === payload) {
        const currentLead = cloneDeep(dialog.lead)

        currentLead.discarded = true
        dialog.lead = currentLead
      }

      return dialog
    })
  },

  SET_DIALOGS_COUNT(state, payload) {
    state.dialogs.count = payload
  },

  SET_DIALOGS_SOURCES(state, payload) {
    state.dialogs.sources = uniqBy(state.dialogs.sources.concat(payload), 'id')
  },

  SET_DIALOGS_READ(state, payload) {
    state.dialogs.dialogs = state.dialogs.dialogs.map(dialog => {
      if (dialog.lead.id === payload.id) {
        dialog.unread = 0
      }

      return dialog
    })
  },

  SET_DIALOGS_TYPING(state, {lead}) {
    state.dialogs.dialogs = state.dialogs.dialogs.map(dialog => {
      if (dialog.lead.id === lead.id) {
        const _lead = cloneDeep(dialog.lead)

        _lead.typing = !dialog.lead.typing

        dialog.lead = _lead
      }

      return dialog
    })
  },

  SET_HISTORY_MESSAGES(state, payload) {
    state.history.messages = payload
  },

  SET_HISTORY_COUNT(state, payload) {
    state.history.count = payload
  },

  SET_HISTORY_PEERS(state, payload) {
    state.history.peers = uniqWith(state.history
      .peers
      .concat(payload), (peer, other) => peer.source === other.source)
  },

  SET_HISTORY_ENTRY(state, payload) {
    if (payload.random) {
      if (state.history.messages.find(message => message.random === payload.random)) {
        state.history.messages = state.history.messages.map(message => {
          if (message.random === payload.random) {
            return payload
          }

          return message
        })
      } else {
        state.history.messages.push(payload)
      }
    } else if (payload.unique) {
      if (state.history.messages.find(message => message.unique === payload.unique)) {
        state.history.messages = state.history.messages.map(message => {
          if (message.unique === payload.unique) {
            payload.send = 1

            return payload
          }

          return message
        })
      } else {
        state.history.messages.push(payload)
      }
    }
  },

  SET_HISTORY_ENTRY_READ(state, payload) {
    state.history.messages = state.history.messages.map(message => {
      if (message.source.id === payload.source.id) {
        message.unread = false
      }

      return message
    })
  },

  DELETE_HISTORY_ENTRY(state, payload) {
    const index = state.history.messages.indexOf(state.history.messages.find(message => message.random === payload.random))

    if (index > -1) {
      state.history.messages.splice(index, 1)
    }
  },

  CLEAR_HISTORY(state) {
    state.history.state = config.dataStates.notAsked
    state.history.messages = []
    state.history.count = 0
    state.history.peers = []
  },

  SET_SOURCES(state, payload) {
    state.sources.sources = uniqWith(payload, 'id')
  },

  SET_SOURCES_ACTIVE(state, payload) {
    state.sources.active = payload
  },
}



