








































































































import Vue from 'vue'
import Component from 'vue-class-component'
import {Watch} from 'vue-property-decorator'
import {debounce} from 'lodash'
import InfiniteLoading from 'vue-infinite-loading'
import MessagesDialogsSearch from './MessagesDialogsSearch.vue'
import DialogItem from '../../../components/global/DialogItem.vue'
import Navigation from '../../../components/Navigation.vue'

@Component({
  components: {Navigation, MessagesDialogsSearch, InfiniteLoading, DialogItem},
  props: {},
})

export default class MessagesDialogs extends Vue {
  repeatCount = 0
  offset = 0
  count = 20
  searchModel = {
    query: '',
    unread: false,
  }

  get dialogs() {
    return this.$store.getters['Messages/dialogs']
  }

  get dialogsCount() {
    return this.$store.getters['Messages/dialogsCount']
  }

  get dialogsSources() {
    return this.$store.getters['Messages/dialogsSources']
  }

  get notAsked() {
    return this.$store.getters['Messages/notAsked']()
  }

  get loading() {
    return this.$store.getters['Messages/loading']()
  }

  get loaded() {
    return this.$store.getters['Messages/loaded']()
  }

  get failed() {
    return this.$store.getters['Messages/failed']()
  }

  get emptySearch() {
    return (this.searchModel.query.length || this.searchModel.unread) && !this.dialogs.length
  }

  get emptySources() {
    return !this.dialogsSources.length
  }

  get emptyLeads() {
    return !this.emptySearch && !this.emptySources && !this.dialogs.length
  }

  @Watch('searchModel')
  onSearchModelChange() {
    this.offset = 0
    this.search()
  }

  search = debounce(() => {
    this.$store.commit('Messages/CLEAR_DIALOGS', [])

    if (this.$refs.infiniteLoading) {
      this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset')
    }
  }, 600)

  repeatFetch() {
    this.offset = 0

    this.$store.commit('Messages/CLEAR_DIALOGS', [])

    if (this.$refs.infiniteLoading) {
      this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset')
    }

    this.repeatCount += 1
  }

  async infiniteHandler($state) {
    const {offset, count} = this
    const {unread, query} = this.searchModel

    const dialogs = await this.$store.dispatch('Messages/dialogs', {
      offset,
      count,
      unread,
      query,
    })

    this.offset += dialogs.length

    if (this.dialogs.length < this.dialogsCount) {
      $state.loaded()
    } else {
      $state.complete()
    }
  }

}



