const Parse = {
  parseArray(r, {target, params}) {
    if (Object.keys(r).length > 1) {
      if (typeof target[0] !== 'object') return []

      const keys = Object.keys(target[0])

      return target.map(targetItem => {
        const mappedKeys = keys.map(i => {

          if (Array.isArray(targetItem[i])) { // если есть вложенность, то мутим рекурсию
            const record = params.find(par => par.name === i)

            if (record) {
              Parse.parseArray(r, {
                target: targetItem[i],
                params: record.children,
              })
            }
          } else { // во всех остальных случаях поступаем как обычно
            let refRecord
            const match = params.find(par => {// поиск совпадений между соотв. записью в доп. массиве и объектом с данными
              if (r[par.helperArray]) {
                refRecord = r[par.helperArray].find(rItem => rItem.id === targetItem[i])
                return par.name === i && refRecord
              }

              return false
            })

            if (match) { // вставляем объект с  данными вместо айдишника
              Object.defineProperty(targetItem, i, {
                value: refRecord,
              })
            }
          }
        }, this).filter(i => i)

        return Object.assign(targetItem, mappedKeys)
      }, this)
    }

    return target
  },

  parseObject: (response, {target, params}) => {
    params.forEach(param => {
      if (target[param.name]) {
        if (param.children) {
          Parse.parseArray(response, {
            target: target[param.name],
            params: param.children,
          })
        } else {
          target[param.name] = response[param.helperArray].find(item => item.id === target[param.name])
        }
      }
    })

    return target
  },
}

export default Parse



