import LeadsApi from '../../../api/Leads'
import config from '../../../../config/config'
import Parse from '../../../utils/Parse'

export default {
  all({commit}, {offset, count, discarded = false}) {
    commit('SET_DATA_STATE', {dataState: config.dataStates.loading})

    const params = [
      {
        name: 'city',
        helperArray: 'cities',
      },
      {
        name: 'source',
        helperArray: 'sources',
      },
      {
        name: 'manager',
        helperArray: 'managers',
      },
      // TODO: Нужен массив для discarded
      // TODO: discard_comment приходит строкой вместо объекта
      {
        name: 'discarded',
        helperArray: 'managers',
      },
    ]

    return LeadsApi
      .all({offset, count, discarded})
      .then((response) => {
        const {count, sources, managers} = response
        let {leads} = response

        try {
          leads = Parse.parseArray(response, {target: leads, params})

          leads = leads.map(lead => {
            lead.managers = lead.managers.map(manager => managers.find(item => item.id === manager))

            return lead
          })
        } catch (error) {
          console.error(error)
        }

        try {
          commit('SET_LEADS', leads)
          commit('SET_COUNT', count)
          commit('SET_OFFSET', offset + count)
          commit('SET_DATA_STATE', {dataState: config.dataStates.loaded})
        } catch (error) {
          console.error(error)
        }
        return {count, leads, sources}
      })
      .catch(() => {
        commit('SET_DATA_STATE', {dataState: config.dataStates.failed})
      })
  },

  getByID({commit}, id) {
    commit('SET_DATA_STATE', {dataState: config.dataStates.loading})

    return LeadsApi
      .getByID({lead: id})
      .then(({lead}) => {
        commit('set_lead', lead)
        commit('set_active_lead', lead.id)

        commit('SET_DATA_STATE', {dataState: config.dataStates.loaded})

        return lead
      })
      .catch((error) => {
        commit('SET_DATA_STATE', {dataState: config.dataStates.failed})

        return Promise.reject(error.body)
      })
  },

  changeManagers({dispatch}, {lead, managers}) {
    return LeadsApi
      .changeManagers(lead, managers)
      .then(response => {
        dispatch('lead_managers', response)

        return response.lead
      })
  },

  assign({commit}, lead) {
    return LeadsApi
      .assign(lead)
      .then(({marked, dialog}) => {
        commit('set_lead_assign', {lead, marked})

        return {marked, dialog}
      })
  },

  search({commit}, {query, offset, count, discarded = false}) {
    commit('SET_DATA_STATE', {dataState: config.dataStates.loading})

    const params = [
      {
        name: 'city',
        helperArray: 'cities',
      },
      {
        name: 'source',
        helperArray: 'sources',
      },
      {
        name: 'manager',
        helperArray: 'managers',
      },
      // TODO: Нужен массив для discarded
      // TODO: discard_comment приходит строкой вместо объекта
      {
        name: 'discarded',
        helperArray: 'managers',
      },
    ]

    return LeadsApi
      .search({query, count, discarded})
      .then(response => {
        const {sources, managers} = response
        let {leads} = response

        try {
          leads = Parse.parseArray(response, {target: leads, params})

          leads = leads.map(lead => {
            lead.managers = lead.managers.map(manager => managers.find(item => item.id === manager))

            return lead
          })
        } catch (error) {
          console.error(error)
        }

        try {
          commit('SET_LEADS', leads)
          commit('SET_COUNT', leads.length)
          commit('SET_OFFSET', offset + leads.length)
          commit('SET_DATA_STATE', {dataState: config.dataStates.loaded})
        } catch (error) {
          console.error(error)
        }
        return {count, leads, sources}
      })
  },

  discard({commit}, {lead, comment}) {
    return LeadsApi
      .discard({lead, comment})
      .then(({marked}) => {
        commit('set_lead_discarded', {lead, marked})

        return marked
      })
  },

  restore({commit}, lead) {
    return LeadsApi
      .restore(lead)
      .then(({marked}) => {
        commit('set_lead_discarded', {lead, marked})

        return {marked}
      })
  },

  connect({commit}, {old_lead, lead, name}) {
    return LeadsApi
      .connect({old_lead, lead, name})
      .then(response => {
        commit('edit_lead', response.lead)

        return response.lead
      })
  },

  add({commit}, data) {
    commit('SET_DATA_STATE', {dataState: config.dataStates.loading})

    return LeadsApi
      .add({...data})
      .then(({lead}) => {
        commit('set_lead', lead)

        commit('SET_DATA_STATE', {dataState: config.dataStates.loaded})
        return lead
      })
      .catch(() => {
        commit('SET_DATA_STATE', {dataState: config.dataStates.failed})
      })
  },

  edit({commit}, data) {
    commit('SET_DATA_STATE', {dataState: config.dataStates.loading})

    return LeadsApi
      .edit({...data})
      .then(({lead}) => {
        commit('edit_lead', lead)

        commit('SET_DATA_STATE', {dataState: config.dataStates.loaded})
        return lead
      })
      .catch(() => {
        commit('SET_DATA_STATE', {dataState: config.dataStates.failed})
      })
  },
  /**
   * Events
   */

  lead_edit({commit}, {lead}) {
    commit('edit_lead', lead)
  },

  lead_connected({commit}, {lead}) {
    //    commit('edit_lead', lead)
  },

  lead_managers({commit}, payload) {
    const params = [
      {
        name: 'city',
        helperArray: 'cities',
      },
      {
        name: 'source',
        helperArray: 'sources',
      },
      {
        name: 'manager',
        helperArray: 'managers',
      },
    ]
    let {lead} = payload
    const {managers} = payload

    try {
      lead = Parse.parseObject(payload, {target: lead, params})

      lead.managers = lead.managers.map(manager => managers.find(item => item.id === manager))
    } catch (error) {
      console.error(error)
    }

    commit('set_lead', lead)
  },
}



