import Vue from 'vue'
import Component from 'vue-class-component'

interface ISource {
  id: number,
  name: string,
  icon: string,
  url: string,
  placeholder: string,
}

@Component
export default class SourcesMixin extends Vue {
  private sourceTypes: ISource[] = [
    {
      id: 0,
      name: 'Vk',
      icon: 'fab fa-vk',
      url: 'https://vk.com/',
      placeholder: 'ID личной страницы',
    },
    {
      id: 1,
      name: 'Vk Group',
      icon: 'fas fa-users',
      url: 'https://vk.com/',
      placeholder: 'ID группы',
    },
    {
      id: 2,
      name: 'Instagram',
      icon: 'fab fa-instagram',
      url: 'https://www.instagram.com/',
      placeholder: 'ID личной страницы',
    },
  ]

  public getSource(id): ISource | undefined {
    return this.sourceTypes.find((source: ISource) => source.id === parseInt(id, 10))
  }
}



