


















































import Vue from 'vue'
import Component from 'vue-class-component'
import Api from '../../../services/api/Api'

@Component({
  props: {
    source: String
  }
})

export default class Login extends Vue {
  $refs = {
    form: HTMLElement
  };

  drawer = null
  valid = false

  phoneRules = [
    v => !!v || 'Телефон обязателен для заполнения',
  ];

  passwordRules = [
    v => !!v || 'Пароль обязателен для заполнения',
  ];

  user = {
    phone: '',
    password: '',
    remember: false
  };

  async submit() {
    if (this.$refs.form.validate()) {
      // Native form submission is not yet supported
      const domain = this.$config.domain;

      const {phone, password, remember} = this.user;

      const result = await Api.post('auth/login', {
        phone, password, remember, domain
      });

      const {authorized, token} = result;

      if (authorized) {
        const expires = '5Y';
        const {cookie} = this.$config;

        if (this.user.remember) {
          cookie.expires = expires
        }

        this.$cookie.set('token', token, cookie)

        location.pathname = '/'
      }
    }
  }
}



