import config from '../../../../config/config'

export default {
  notAsked(state) {
    return state.state === config.dataStates.notAsked
  },
  loading(state) {
    return state.state === config.dataStates.loading
  },
  loaded(state) {
    return state.state === config.dataStates.loaded
  },
  failed(state) {
    return state.state === config.dataStates.failed
  },

  system(state) {
    return state.system
  }
}



