/*
Если нужны и excerpt и highlight то применять их в указанном порядке (1.excerpt 2.highlight)

 */

let snippeter = {
  /**
   * Взял отсюда https://www.npmjs.com/package/excerpt и переделал
   * До переделки рубил слова.
   * До переделки отдават только сокращенную версию, если не находил фразу в тексте. Теперь в этом
   * случае может отдавать полный текст. указанный радиус будет максимальным. На практике скорее
   * всего будет меньше, т.к. куки слов по краям обрежуться.
   */
  excerpt: function (text, phrase, radius, ending, shortIfNotFound) {

    var append, prepend, phraseLen, pos, startPos, endPos, excerpt, textLen
    if (!text || !phrase) {
      return text
    }
    shortIfNotFound = shortIfNotFound || false
    radius = radius || '100'
    ending = ending || '...'
    append = prepend = ending
    phraseLen = phrase.length
    textLen = text.length
    pos = text.toLowerCase().indexOf(phrase.toLowerCase())
    // if (pos === false) { //было
    if (pos < 0) {              //стало
      if (shortIfNotFound) {
        return text.substr(0, radius) + ending
      } else {
        return text    //переделал.
      }
    }
    startPos = pos - radius

    if (startPos <= 0) {
      startPos = 0
      prepend = ''
    }
    endPos = pos + phraseLen + radius

    if (endPos >= textLen) {
      endPos = textLen
      append = ''
    }
    excerpt = text.substr(startPos, endPos - startPos)

    //переделал здесь, чтоб не рубило
    let excerptWords = excerpt.split(' ')
    if (append != '') excerptWords.pop()
    if (prepend != '') excerptWords.shift()
    excerpt = excerptWords.join(' ')
    //переделал здесь end

    excerpt = prepend + excerpt + append

    return excerpt
  },
  /**
   * https://stackoverflow.com/a/280805/1638298
   * used in this.highlight
   * @param str
   * @returns {string}
   * @private
   */
  _preg_quote: function preg_quote(str) {
    return (str + '').replace(/([\\\.\+\*\?\[\^\]\$\(\)\{\}\=\!\<\>\|\:])/g, '\\$1')
  },

  /**
   * https://stackoverflow.com/a/280805/1638298
   * @param data
   * @param search
   */
  highlight: function (data, search) {
    if (search && search.length) {
      return data.replace(new RegExp('(' + search + ')', 'gi'), '<span class=\'highlighted\'>$1</span>')
    }

    return data
  },
}

export default snippeter



