import Api from './Api'

interface IPlannerRequest {
  from: number,
  to: number,
  offset: number,
  count: number,
  reverse: number
}

export default {
  /**
   * Метод получает список всех задач, поставленных лидам.
   *
   * @param {IPlannerRequest} request
   * @returns {AxiosPromise<any>}
   */
  planner(request: IPlannerRequest) {
    return Api.get('hot/planner', request)
  },
}



