import Auth from '../../views/auth/index.vue'
import Login from '../../views/auth/login/index.vue'
import Utils from '../utils/utils'

export default {
  path: '/auth',
  component: Auth,
  name: 'auth',
  children: [
    {
      path: '/login',
      component: Login,
      name: 'login',
      meta: {
        title: Utils.getTitle('Авторизация'),
      },
    }
  ]
}


