import moment from 'moment'

moment.locale('ru');

declare var process: {
  env: {
    NODE_ENV: string
  }
};

interface IDataStates {
  notAsked: string,
  loading: string,
  loaded: string,
  failed: string,
}

interface IDefaultConfig {
  environment: string,
  dataStates: IDataStates,
  domain: string,
  cookie: object,
  apiUrls: {
    default: string,
  },

  metrics: {
    yandex: {
      ID: number,
    },
  },
}

const host = location.host.split('.')

const defaultConfig = {
  environment: process.env['NODE_ENV'],
  dataStates: {
    notAsked: 'notAsked',
    loading: 'loading',
    loaded: 'loaded',
    failed: 'failed',
  },
  domain: host.length === 3 ? host[0] : '',
  cookie: {},
  apiUrls: {
    default: 'https://api-dev.hivecrm.ru/',
  },


  metrics: {
    yandex: {
      ID: 47410063,
    },
  },
};

interface IConfig {
  development: IDefaultConfig,
  production: IDefaultConfig,

  [key: string]: IDefaultConfig
}

const config: IConfig = {
  development: {
    ...defaultConfig,
    domain: 'develop'
  },
  production: {
    ...defaultConfig,
    cookie: {domain: defaultConfig.domain}
  }
};


export default config[process.env['NODE_ENV']]


