













import Vue from 'vue'
import Component from 'vue-class-component'
import HotDialogs from './HotDialogs.vue'
import ProcessDialogs from './ProcessDialogs.vue'
import PlannerDialogs from './PlannerDialogs.vue'

@Component({
  components: {HotDialogs, ProcessDialogs, PlannerDialogs},
  props: {},
})

export default class Hot extends Vue {
  activeTab = '0'
  tabs = [
    {
      name: 'hot',
      title: 'Новые заявки',
      icon: 'fas fa-fire',
      component: 'hot-dialogs',
    }, {
      name: 'process',
      title: 'В процессе',
      icon: 'fas fa-sync',
      component: 'process-dialogs',
    }, {
      name: 'planner',
      title: 'Планировщик',
      icon: 'far fa-calendar-check',
      component: 'planner-dialogs',
    },
  ]

  beforeRouteLeave(to, from, next) {
    if (to.name === 'messages.account') {
      const dialog = this.$store.getters['Hot/hotDialog'](to.params.lead)

      if (dialog) {
        this.$store.commit('Messages/SET_DIALOG', dialog)
      }
    }

    next()
  }
}



