import config from '../../../../config/config'

import actions from './actions'
import getters from './getters'
import mutations from './mutations'

const state = {
  hot: {
    state: config.dataStates.notAsked,
    dialogs: [],
    sources: [],
    count: 0,
  },

  process: {
    state: config.dataStates.notAsked,
    dialogs: [],
    sources: [],
    count: 0,
  },

  hotPlanner: {
    count: null,
    items: [],
    leads: [],
    managers: [],
    products: [],
    sources: [],

    reverse: 0,
    offset: 0,
    state: config.dataStates.notAsked,
  },

  query: 'asf',
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



