import Api from './Api'
import {AxiosPromise, AxiosResponse} from "axios";

interface IDialogsParams {
  offset: number,
  count: number,
  reverse: number,
  hot?: boolean,
  process?: boolean,
  unread?: boolean,
  query?: string,
}

interface IHistoryParams {
  offset: number,
  account: number,
  count?: number,
}

interface IHistorySendParams {
  random: number,
  source: number,
  account: number,
  author: number,
  text: string,
  attachments?: Array<object>,
}

interface IDialogsResponse {
  count: number
  sources: Array<object>
  dialogs: Array<object>
}

export default {

  /**
   * Получение всех диалогов
   *
   * @param {IDialogsParams} params
   * @param {string} url
   * @returns {IDialogsResponse}
   */
  dialogs(params: IDialogsParams, url: string = ''): AxiosPromise<IDialogsResponse> {
    return Api.get(`${url}messages/dialogs`, params)
  },

  /**
   * Получение сообщений
   * @param {IHistoryParams} params
   * @param {string} url
   * @returns {any}
   */
  history(params: IHistoryParams, url: string = '') {
    return Api.get(`${url}messages/history`, params)
  },

  /**
   * Отправка сообщения
   *
   * @param {IHistorySendParams} params
   * @param {string} url
   * @returns {AxiosPromise<any>}
   */
  sendMessage(params: IHistorySendParams, url: string = '') {
    return Api.get(`${url}messages/send`, params)
  },


  /**
   * Прочтение сообщений
   *
   * @param {{account: number}} params
   * @param {string} url
   * @returns {AxiosPromise<any>}
   */
  read(params: { account: number }, url: string = '') {
    return Api.post(`${url}messages/read`, params)
  }
}



