













































import Vue from 'vue'
import Component from 'vue-class-component'
import {Watch} from 'vue-property-decorator'
import {debounce} from 'lodash'
import InfiniteLoading from 'vue-infinite-loading'
import Sources from '../../../components/_mixins/Sources'
import DialogMessageInput from '../../../components/global/DialogMessageInput/index.vue'
import MessagesItem from './MessagesItem.vue'

@Component({
    mixins: [Sources],
    components: {InfiniteLoading, DialogMessageInput, MessagesItem},
    props: {
        value: Number,
    },
})

export default class MessagesItems extends Vue {
    count = 10
    offset = 0
    sendObject = {
        message: '',
        attachments: [],
    }

    @Watch('account')
    onAccountChange() {
        this.offset = 0
        this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset')
        this.$store.commit('Messages/CLEAR_HISTORY')
    }

    get account() {
        return this.$store.getters['Leads/getActiveAccount']
    }


    get activeLead() {
        return this.$store.getters['Leads/getActiveLead']
    }

    get messages() {
        return this.$store.getters['Messages/messages']
    }

    get messagesCount() {
        return this.$store.getters['Messages/messagesCount']
    }

    get notAsked() {
        return this.$store.getters['Messages/notAsked']('history')
    }

    get loading() {
        return this.$store.getters['Messages/loading']('history')
    }

    get loaded() {
        return this.$store.getters['Messages/loaded']('history')
    }

    get failed() {
        return this.$store.getters['Messages/failed']('history')
    }

    get additionalClass() {
        return {
            'messages_not-asked': this.notAsked,
            'messages_loading': this.loading,
            'messages_loaded': this.loaded,
            'messages_failed': this.failed,
        }
    }

    onScroll(event) {
        // console.log(event)
    }

    scrollToBottom() {
        if (this.$refs.messagesItems) {
            this.scrollTo(this.$refs.messagesItems.clientHeight)
        }
    }

    scrollTo(value: number) {
        window.scrollTo(0, value)
    }

    async infiniteHandler($state) {
        const {count, offset} = this
        const scrollHeightBeforeAdding = this.$refs.messagesItems.clientHeight

        const messages = await this.$store.dispatch('Messages/history', {
            offset,
            count,
            account: this.account.id,
        })

        this.offset += messages.length

        if (this.messages.length < this.messagesCount) {
            const scrollHeightAfterAdding = this.$refs.messagesItems.clientHeight

            $state.loaded()

            this.scrollTo(scrollHeightAfterAdding - scrollHeightBeforeAdding)
        } else {
            $state.complete()
        }
    }

    updated() {
        console.log('updated')
        // this.scrollToBottom()
    }

    mounted() {
        this.scrollToBottom()

        if (this.$refs.scrollContainer) {
            this.$refs.scrollContainer.scrollToBottom()
        }
    }
}



