import config from '../../../../config/config'

import actions from './actions'
import getters from './getters'
import mutations from './mutations'

const state = {
  state: config.dataStates.notAsked,
  isLoading: false,
  leads: [], // Массив лидов
  offset: null,
  sources: [], // Массив источников
  count: null, // Общее число лидов в системе
  activeLead: null,
  activeAccount: null,
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



