























import Vue from 'vue'
import Component from 'vue-class-component'
import {debounce} from 'lodash'
import InfiniteLoading from 'vue-infinite-loading'
import DialogItem from '../../../components/global/DialogItem.vue'

@Component({
  components: {InfiniteLoading, DialogItem},
  props: {
    value: Object,
    loading: {
      type: Boolean,
      default: false,
    },
  },
})

export default class MessagesDialogsSearch extends Vue {
  get badgeColor() {
    return this.value.unread ? 'red' : 'grey'
  }

  toggleUnread() {
    this.value.unread = !this.value.unread
    this.emit()
  }

  input() {
    this.emit()
  }

  emit() {
    this.$emit('input', {
      query: this.value.query,
      unread: this.value.unread,
    })
  }
}



