


















































































import Vue from 'vue'
import Component from 'vue-class-component'
import InfiniteLoading from 'vue-infinite-loading'
import DialogItem from '../../components/global/DialogItem.vue'

@Component({
  components: {InfiniteLoading, DialogItem},
  props: {},
})

export default class HotDialogs extends Vue {
  reverse = false
  count = 20
  offset = 0
  hot = true
  repeatCount = 0

  get query() {
    return this.$store.getters['Hot/query']
  }

  get dialogs() {
    return this.$store.getters['Hot/hot']
  }

  get dialogsCount() {
    return this.$store.getters['Hot/hotCount']
  }

  get notAsked() {
    return this.$store.getters['Hot/notAsked']()
  }

  get loading() {
    return this.$store.getters['Hot/loading']()
  }

  get loaded() {
    return this.$store.getters['Hot/loaded']()
  }

  get failed() {
    return this.$store.getters['Hot/failed']()
  }

  repeatFetch() {
    this.offset = 0

    this.$store.commit('Hot/CLEAR_HOT', [])

    if (this.$refs.infiniteLoading) {
      this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset')
    }

    this.repeatCount += 1
  }

  async infiniteHandler($state) {
    const {reverse, offset, hot, count} = this

    const dialogs = await this.$store.dispatch('Hot/hot', {
      reverse,
      offset,
      hot,
      count,
      query: this.query,
    })

    this.offset += dialogs.length

    if (this.dialogs.length < this.dialogsCount) {
      $state.loaded()
    } else {
      $state.complete()
    }
  }

  created() {
    // console.log('hotDialog')
    // this.infiniteHandler()
  }
}



