





















































































































































import Vue from 'vue'
import Component from 'vue-class-component'
// import {Watch} from 'vue-property-decorator'
import moment from 'moment'
import {debounce} from 'lodash'
import Sources from '../../../components/_mixins/Sources'

@Component({
  mixins: [Sources],
  components: {},
  props: {
    message: Object,
    account: Object,
  },
})

export default class MessagesItem extends Vue {
  get messageAdditionalClass() {
    return {
      'f-message_unread': this.message.unread,
      'f-message_sibling': this.message.sibling,
      'f-message_forward': this.message.type === 2,
      'f-message_comment': this.message.type === 3,
      'f-message_out': this.message.out,
      'f-message_in': !this.message.out,
    }
  }

  get avatar() {
    return this.message.out ? this.message.source.avatar : this.account.avatar
  }

  get linkToSocial() {
    let link = '#'
    let accountLink = this.account.link

    if (this.message.source) {
      if (this.message.out) {
        accountLink = this.message.source.link
      }

      const source = this.getSource(this.account.type)

      if (source) {
        link = `${source.url}${accountLink}`
      }
    }

    return link
  }

  get sourceLink() {
    return `${this.getSource(this.message.source.type).url}${this.message.source.link}`
  }

  get text() {
    // eslint-disable-next-line
    const re = /([^\"=]{2}|^)((https?|ftp):\/\/\S+[^\s.,> )\];'\"!?])/
    // eslint-disable-next-line
    const subst = '$1<a href="$2" target="_blank">$2</a>'

    let withlink = null

    if (this.message.text) {
      withlink = this.message.text.replace(re, subst)
      // withlink = this.message.text
    }

    return withlink
  }

  get forwarded() {
    return this.message.forwarded
  }

  get type() {
    return this.message.type
  }

  get comment() {
    return this.type === 3
  }

  get commentClass() {
    let postType = {}

    if (this.message.post) {
      postType = {
        'f-message__body-text-wrap_comment-post': this.message.post.type === 0,
        'f-message__body-text-wrap_comment-photo': this.message.post.type === 1,
        'f-message__body-text-wrap_comment-video': this.message.post.type === 2,
        'f-message__body-text-wrap_comment-product': this.message.post.type === 3,
        'f-message__body-text-wrap_comment-discussion': this.message.post.type === 4,
      }
    }

    return {
      'f-message__body-text-wrap_comment': this.comment,
      ...postType,
    }
  }

  get name() {
    return this.message.out ? this.message.source.name : this.account.name
  }

  get date() {
    const {stamp} = this.message
    const lastPeerDate = moment.unix(stamp).toArray()
    const currentDate = moment().toArray()

    lastPeerDate.splice(3, 4)
    currentDate.splice(3, 4)

    const a = moment(lastPeerDate)
    const b = moment(currentDate)

    if (+b.format('YYYY') !== +a.format('YYYY')) {
      return moment.unix(stamp).format('DD MMM YYYY')
    }

    if (b.diff(a, 'days') === 1) {
      return 'Вчера'
    }

    if (b.diff(a, 'days')) {
      return moment.unix(stamp).format('DD MMM')
    }

    return moment.unix(stamp).format('HH:mm')


    // return worder.timeStampToMessageTime(this.message.stamp)
  }

  get sourceIcon() {
    return this.getSource(this.message.source.type).icon
  }

  get messageAdditionalClass() {
    return {
      'f-message_unread': this.message.unread,
      'f-message_sibling': this.message.sibling,
      'f-message_forward': this.message.type === 2,
      'f-message_comment': this.message.type === 3,
      'f-message_out': this.message.out,
      'f-message_in': !this.message.out,
    }
  }

  get notSend() {
    return !this.message.send
  }

  get status() {
    return {
      'fa-check': this.message.send === 1,
      'fa-clock-o': this.message.send === 2,
      'fa-times': !this.message.send,
      'grey--text lighten-2': this.message.send === 1,
      'red--text accent-4': !this.message.send,
    }
  }

  mounted() {
  }
}



