








































import Vue from 'vue'
import Component from 'vue-class-component'

@Component({
  props: {
    app: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    },
    showTitle: {
      type: Boolean,
      default: true
    },
    flat: {
      type: Boolean,
      default: false
    }
  },
})

export default class Navigation extends Vue {
  miniVariant = false;
  drawer = false;
  clipped = true;

  items = [
    {
      name: 'desktop',
      count: 0,
      title: 'Рабочий стол',
      icon: 'dashboard',
      disabled: true,
      to: {
        name: 'desktop'
      }
    },
    {
      name: 'hot',
      count: 0,
      title: 'Горячие',
      icon: 'fire',
      disabled: false,
      to: {
        name: 'hot'
      }
    },
    {
      name: 'messages',
      count: 0,
      title: 'Диалоги',
      icon: 'dialogs',
      disabled: false,
      to: {
        name: 'messages'
      }
    },
    {
      name: 'leads',
      count: 0,
      title: 'Лиды',
      icon: 'bee',
      disabled: true,
      to: {
        name: 'leads'
      }
    },
    {
      name: 'products',
      count: 0,
      title: 'Товары и услуги',
      icon: 'products',
      disabled: true,
      to: {
        name: 'products'
      }
    },
    {
      name: 'planner',
      count: 0,
      title: 'Задачи',
      icon: 'tasks',
      disabled: true,
      to: {
        name: 'tasks'
      }
    },
    {
      name: 'settings',
      count: 0,
      title: 'Настройки',
      icon: 'gear',
      disabled: true,
      to: {
        name: 'settings'
      }
    }
  ];

  get title() {
    return this.$route.meta ? this.$route.meta.title || '' : ''
  }

  toggleNav() {
    this.drawer = !this.drawer
  }
}



