import config from '../../../../config/config'

export default {
  notAsked: (state) => (type = 'dialogs') => state[type].state === config.dataStates.notAsked,

  loading: (state) => (type = 'dialogs') => state[type].state === config.dataStates.loading,

  loaded: (state) => (type = 'dialogs') => state[type].state === config.dataStates.loaded,

  failed: (state) => (type = 'dialogs') => state[type].state === config.dataStates.failed,

  dialogs(state) {
    return state.dialogs.dialogs
  },

  dialogsCount(state) {
    return state.dialogs.count
  },

  dialogsSources(state) {
    return state.dialogs.sources
  },

  typingDialog(state) {
    return state.dialogs.dialogs.find((dialog) => dialog.lead.typing)
  },

  messages(state) {
    return state.history.messages
  },

  messagesCount(state) {
    return state.history.count
  },

  peersNotReady(state) {
    return state.history.peers.filter((peer) => !peer.ready)
  },

  sources(state) {
    return state.sources.sources
  },

  activeSource(state) {
    return state.sources.active
  },
}



