import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify, {
  theme: {
    primary: '#d39c00',
    secondary: '#424242',
    placeholder: '#b4bccc',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#7aa1e3',
    success: '#4CAF50',
    warning: '#FFC107',
  },
})



