





































import Vue from 'vue'
import Component, {mixins} from 'vue-class-component'
import {Watch} from 'vue-property-decorator'
import moment from 'moment'
import snippeter from '../../services/utils/snippeter'
import SourceMixin from '../_mixins/Sources'

@Component({
  props: {
    dialog: Object,
    activeLead: Number,
    showSource: {
      type: Boolean,
      default: false,
    },
    showOnlyAvatar: {
      type: Boolean,
      default: false,
    },
    remove: {
      type: Boolean,
      default: false,
    },
    removeText: {
      type: String,
      default: 'Удалить',
    },
    searchText: {
      type: String,
      default: '',
    },
  },
})

export default class DialogItem extends mixins(Vue, SourceMixin) {
  timeout = null

  get lead() {
    return this.dialog.lead
  }

  get leadName() {
    return snippeter.highlight(this.lead.name, this.searchText)
  }

  get last_peer() {
    return this.dialog.last_peer
  }

  get last_peer_message() {
    if (!this.last_peer.ready || !this.last_peer.last_message) {
      return 'Все диалоги будут доступны чуть позже'
    }

    const out = this.dialog.last_peer.last_message.out ? 'Вы: ' : ''

    if (!this.last_peer.last_message.text) {
      return `${out}[Вложение]`
    }

    let {text} = this.last_peer.last_message

    return `${out}${text}`
  }

  get routerLink() {
    if (this.last_peer && this.last_peer.account) {
      return {
        name: 'messages.account',
        params: {
          lead: this.lead.id,
          account: this.last_peer.account,
        },
      }
    }

    return {
      name: 'messages.lead',
      params: {
        lead: this.lead.id,
      },
    }
  }

  get unread() {
    return this.dialog.unread
  }

  get time() {
    const lastPeerDate = moment.unix(this.last_peer.stamp).toArray()
    const currentDate = moment().toArray()

    lastPeerDate.splice(3, 4)
    currentDate.splice(3, 4)

    const a = moment(lastPeerDate)
    const b = moment(currentDate)

    if (+b.format('YYYY') !== +a.format('YYYY')) {
      return moment.unix(this.last_peer.stamp).format('DD MMM YYYY')
    }

    if (b.diff(a, 'days') === 1) {
      return 'Вчера'
    }

    if (b.diff(a, 'days')) {
      return moment.unix(this.last_peer.stamp).format('DD MMM')
    }

    return moment.unix(this.last_peer.stamp).format('HH:mm')
  }

  get isActiveLead() {
    return this.lead.id === this.activeLead
  }

  get source_account() {
    if (!this.last_peer) {
      return ''
    }

    const account = this.lead.accounts.find(item => item.id === this.last_peer.account)

    if (account) {
      return account.type
    }

    return ''
  }

  @Watch('lead')
  onLeadChange() {
    const {typing} = this.lead

    if (!typing) {
      if (this.timeout) {
        clearTimeout(this.timeout)
      }
    }

    if (typing) {
      if (this.timeout) {
        clearTimeout(this.timeout)
      }

      this.timeout = setTimeout(() => {
        this.$store.dispatch('events/message_typing', {lead: this.lead})
      }, 3000)
    }
  }

  removeItem(event) {
    event.preventDefault()
    event.stopPropagation()

    this.$emit('removeDialog', this.dialog)
  }
}



