import config from '../../../../config/config'

export default {
  notAsked: state => state.state === config.dataStates.notAsked,

  loading: state => state.state === config.dataStates.loading,

  loaded: state => state.state === config.dataStates.loaded,

  failed: state => state.state === config.dataStates.failed,

  all(state) {
    return state.leads
  },

  allByOffset: state => (offset, count) => {
    let leads = state.leads.slice(offset, offset + count)

    if (!leads.length) {
      const stateLeadsLength = state.leads.length
      leads = state.leads.slice(stateLeadsLength - count, stateLeadsLength)
    }

    return leads
  },

  offset(state) {
    return state.offset
  },

  count(state) {
    return state.count
  },

  getByID: state => id => {
    return state.leads.find(lead => lead.id === id)
  },

  getActiveLead(state) {
    return state.leads.find(lead => lead.id === state.activeLead) || {}
  },

  getSources(state) {
    return state.sources
  },

  getActiveAccount(state) {
    const lead = state.leads.find(lead => {
      let localLead = lead.accounts.find(account => {
        return account.id === state.activeAccount
      })

      if (localLead) {
        return lead
      }
    })

    if (lead) {
      return lead.accounts.find(account => account.id === state.activeAccount) || {}
    }

    return {}
  },
}



